import React, { PureComponent } from 'react'
import { Form, Input, Select, Button, Checkbox, Divider } from 'antd'

const { Option } = Select;
export default class addDevice extends PureComponent {
  formRef = React.createRef()
  state = {
    //'OPC_DA', 
    protocolNames: ['Modbus_TCP', 'S7_TCP', 'OPC_UA', 'MC3E_Binary_Ethernet', 'MCA1E_Binary_Ethernet', 'Fins_TCP'],//
    suppliers: {//
      'Modbus_TCP': ['', 'Siemens'],
      'S7_TCP': ['Siemens'],
      // 'OPC_DA': [''],
      'OPC_UA': [''],
      'MC3E_Binary_Ethernet': [''],
      'MCA1E_Binary_Ethernet': [''],
      'Fins_TCP': ['Omron']
    },
    modelLists: {//
      'Modbus_TCP': [''],
      'S7_TCP': ['S7-300/400/1200/1500', 'S7-200Smart'],
      // 'OPC_DA': [''],
      'OPC_UA': [''],
      'MC3E_Binary_Ethernet': [''],
      'MCA1E_Binary_Ethernet': [''],
      'Fins_TCP': ['']
    },
    isShowAttr: { //
      'Modbus_TCP': ["modelLists", "IPAddress", "DeviceID", "Port", "TimeOut", "ByteOrder", "StrByteOrder"],
      'S7_TCP': ["modelLists", "IPAddress", "Rack", "Slot"],
      // 'OPC_DA': ["IPAddress", "ServerName", "GroupName", "UpdateRate", "DeadBand", "IsActive"],
      'OPC_UA': ["SessionName", "IPAddress", "SecurityMode", "SecurityPolicy", "UserIdentity", "userName", "Password"],
      'MC3E_Binary_Ethernet': ["IPAddress", "Port", "StrByteOrder"],
      'MCA1E_Binary_Ethernet': ["IPAddress", "Port", "StrByteOrder"],
      'Fins_TCP': ["IPAddress", "Port", "StrByteOrder"]
    },
    activeProto: "Modbus_TCP", // 
    initialValues: {
      id: "00000000-0000-0000-0000-000000000000",
      name: "",
      desc: "",
      // nodeType: "3",
      protocolName: "Modbus_TCP", //
      supplier: "",//
      model: "",
      IPAddress: "127.0.0.1",
      DeviceID: "1234",
      Port: "5000",
      TimeOut: "5000",
      ByteOrder: "1234",
      IsActive: "True",
      SecurityMode: "",
      SecurityPolicy: "",
      UserIdentity: "",
      StrByteOrder1: "false",
      StrByteOrder: "1234"
    },//
    userPane: false,
  }
  //
  isShowFormItem = item => {
    if (this.state.activeProto) {
      return this.state.isShowAttr[this.state.activeProto].indexOf(item) < 0 ? false : true
    }
  }
  //
  onProtocalNameChange = e => {
    this.setState({ activeProto: e }, () => {
      //
      this.formRef.current.setFieldsValue({
        supplier: this.state.suppliers[e][0],
        model: this.state.modelLists[e][0],
      });
    })
  }
  //
  onChangUserIdentity = e => {
    e === "" ? this.setState({ userPane: true }) : this.setState({ userPane: false })
  }
  componentDidMount() {
    console.log(this.props)
    if (this.props.node) {
      // 
      const node = this.props.node,
        initValue = {
          id: node.id,
          name: node.name,
          desc: node.desc,
          // nodeType: "3",
          protocolName: node.protocolName, //
          supplier: node.supplier,//
          model: node.model,
        };
      for (let i in node.params) {
        if (i === "StrByteOrder") {
          if (node.protocolName === "Fins_TCP" || node.protocolName === "MCA1E_Binary_Ethernet" || node.protocolName === "MC3E_Binary_Ethernet") {
            initValue.StrByteOrder1 = node.params[i] === "True" ? true : false
            console.log(initValue.StrByteOrder1)
          } else {
            initValue.StrByteOrder = node.params[i]
          }
        } else {
          initValue[i] = node.params[i]
        }
      }
      if (node.params.UserIdentity === "") {
        this.setState({ userPane: true })
      }
      //
      this.formRef.current.setFieldsValue(initValue);
      this.setState({
        activeProto: node.protocolName,
        edit: true
      })
    }
  }

  //opc_ua
  connetTest = () => {
    let formData = this.formRef.current.getFieldsValue()
    this.formRef.current.validateFields().then(() => {
      this.props.connetTest({
        serverName: formData.IPAddress,
        type: formData.UserIdentity,
        username: formData.UserName || "",
        password: formData.Password || ""
      })
    })
  }

  render() {
    const { protocolNames, suppliers, modelLists, initialValues, activeProto, edit } = this.state
    return (
      <Form
        ref={this.formRef}
        onFinish={this.props.onFinish}
        initialValues={initialValues}
      >
        <div className="form-table">
          <Form.Item label="ID" name="id" hidden={true}>
            <Input  autoComplete="off"/>
          </Form.Item>
          <Form.Item label="" name="name"
            rules={[
              {
                required: true,
                message: '',
              }
            ]}
          >
            <Input  autoComplete="off"/>
          </Form.Item>
          <Form.Item label="" name="desc" placeholder="">
            <Input  autoComplete="off"/>
          </Form.Item>
        </div>
        <Divider></Divider>
        <div className="form-table">
          {/* <Form.Item label="" name="nodeType" hidden={true} >
            <Input />
          </Form.Item> */}
          <Form.Item label="" name="protocolName" >
            <Select onChange={this.onProtocalNameChange} disabled={edit}>
              {
                protocolNames.map(e => {
                  return <Option value={e} key={e}>{e}</Option>
                })
              }
            </Select>
          </Form.Item>
          <Form.Item label="" name="supplier">
            <Select disabled={edit}>
              {
                suppliers[activeProto] ? suppliers[activeProto].map(e => {
                  return <Option value={e} key={e}>{e}</Option>
                }) : null
              }
            </Select>
          </Form.Item>
          <Form.Item label="" name="model" hidden={!this.isShowFormItem("modelLists")}>
            <Select disabled={edit}>
              {
                modelLists[activeProto].map((e, idx) => {
                  return <Option value={e} key={idx}>{e}</Option>
                })
              }
            </Select>
          </Form.Item>
          {
            this.isShowFormItem("SessionName") ? (
              <Form.Item label="" name="SessionName"
                rules={[
                  {
                    required: true,
                    message: "",
                  }
                ]}>
                <Input  autoComplete="off"/>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("IPAddress") ? (
              <Form.Item label={this.state.activeProto === "OPC_UA" ? "" : "IP"} name="IPAddress"
                rules={[
                  {
                    required: true,
                    message: ` ${this.state.activeProto === "OPC_UA" ? "" : "IP"}`,
                  },
                  // {
                  //   pattern: /^((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})(\.((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})){3}$/,
                  //   message: '',
                  // }
                ]}
              >
                <Input  autoComplete="off"/>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("DeviceID") ? (
              <Form.Item label="ID" name="DeviceID" placeholder="ID"
                rules={[
                  {
                    required: true,
                    message: 'ID',
                  }, {
                    pattern: /^\+?[0-9]*$/,
                    message: '',
                  }
                ]}
              >
                <Input  autoComplete="off"/>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("Port") ? (
              <Form.Item label="" name="Port" placeholder=""
                rules={[
                  {
                    required: true,
                    message: '',
                  }, {
                    pattern: /^\+?[1-9][0-9]*$/,
                    message: '0',
                  }
                ]}
              >
                <Input type="Number" autoComplete="off"/>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("TimeOut") ? (
              <Form.Item label="" name="TimeOut" placeholder=""
                rules={[
                  {
                    required: true,
                    message: '',
                  }, {
                    pattern: /^\+?[1-9][0-9]*$/,
                    message: '0',
                  }
                ]}
              >
                <Input suffix="" autoComplete="off" />
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("ByteOrder") ? (
              <Form.Item label="32" name="ByteOrder">
                <Select>
                  <Option value="1234">1234</Option>
                  <Option value="2143">2143</Option>
                  <Option value="3412">3412</Option>
                  <Option value="4321">4321</Option>
                </Select>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("ServerName") ? (
              <Form.Item label="" name="ServerName" placeholder=""
                rules={[
                  {
                    required: true,
                    message: '',
                  }
                ]}
              >
                <Input autoComplete="off" />
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("GroupName") ? (
              <Form.Item label="" name="GroupName" placeholder=""
                rules={[
                  {
                    required: true,
                    message: '',
                  }
                ]}
              >
                <Input autoComplete="off" />
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("UpdateRate") ? (
              <Form.Item label="" name="UpdateRate" placeholder=""
                rules={[
                  {
                    required: true,
                    message: '',
                  }, {
                    pattern: /^\+?[1-9][0-9]*$/,
                    message: '0',
                  }
                ]}
              >
                <Input suffix="mSec" autoComplete="off" />
              </Form.Item>
            ) : null
          }{
            this.isShowFormItem("DeadBand") ? (
              <Form.Item label="" name="DeadBand" placeholder=""
                rules={[
                  {
                    required: true,
                    message: '',
                  }, {
                    pattern: /^\+?[1-9][0-9]*$/,
                    message: '0',
                  }
                ]}
              >
                <Input type="number" suffix="%FS" autoComplete="off" />
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("IsActive") ? (
              <Form.Item label="" name="IsActive" >
                <Select>
                  <Option value="True">True</Option>
                  <Option value="False">False</Option>
                </Select>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("SecurityMode") ? (
              <Form.Item label="" name="SecurityMode">
                <Select>
                  <Option value=""></Option>
                </Select>
              </Form.Item>
            ) : null
          }{
            this.isShowFormItem("SecurityPolicy") ? (
              <Form.Item label="" name="SecurityPolicy">
                <Select>
                  <Option value=""></Option>
                </Select>
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("UserIdentity") ? (
              <Form.Item label="" name="UserIdentity">
                <Select onChange={this.onChangUserIdentity}>
                  <Option value=""></Option>
                  <Option value=""></Option>
                </Select>
              </Form.Item>
            ) : null
          }{
            this.state.userPane ? (
              <Form.Item label="" name="UserName" rules={
                [{
                  required: true,
                  message: ""
                }]
              }>
                <Input autoComplete="off" />
              </Form.Item>
            ) : null
          }{
            this.state.userPane ? (
              <Form.Item label="" name="Password" rules={
                [{
                  required: true,
                  message: ""
                }]
              }>
                <Input.Password autoComplete="off" />
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("Rack") ? (
              <Form.Item label="" name="Rack" rules={
                [{
                  required: true,
                  message: ""
                }]
              }>
                <Input autoComplete="off" />
              </Form.Item>
            ) : null
          }
          {
            this.isShowFormItem("Slot") ? (
              <Form.Item label="CPU" name="Slot" rules={
                [{
                  required: true,
                  message: "CPU"
                }]
              }>
                <Input autoComplete="off" />
              </Form.Item>
            ) : null
          }{
            this.isShowFormItem("StrByteOrder") ?
              (
                this.state.activeProto === "Fins_TCP" || this.state.activeProto === "MCA1E_Binary_Ethernet" || this.state.activeProto === "MC3E_Binary_Ethernet" ?
                  (
                    <Form.Item valuePropName="checked" name="StrByteOrder1" className="form-block">
                      <Checkbox></Checkbox>
                    </Form.Item>
                  ) : (
                    <Form.Item label="" name="StrByteOrder"
                      rules={[{
                        require: true
                      }]
                      }>
                      <Select>
                        <Option value="1234">1234</Option>
                        <Option value="2143">2143</Option>
                        <Option value="3412">3412</Option>
                        <Option value="4321">4321</Option>
                      </Select>
                    </Form.Item>
                  )
              ) : null
          }
        </div>
        <Form.Item className="form-footer" style={{ paddingTop: '20px' }}>
          {
            this.state.activeProto === "OPC_UA" ?
              <>
                <Button type="default" className="login-form-button" onClick={this.connetTest}></Button>
                <span className="divider" style={{ marginRight: 0 }}></span>
              </> : <></>
          }
          <Button type="default" className="login-form-button" onClick={this.props.onCancel}></Button>
          <Button type="primary" htmlType="submit" className="login-form-button" ref="submit">
            
          </Button>
        </Form.Item>
      </Form>
    )
  }
}